@keyframes loader {
    0% {
        transform: scale(0);
        opacity: 1;
    }
    100%{
        transform: scale(1);
        opacity: 0;
    }
}

.loader::after,
.loader::before{
    animation: loader 2s linear infinite;
    content: '';
    border: 8px solid white;
    width: 150px;
    height: 150px;
    position: absolute;
    top: 325px;
    left: 258px;
    border-radius: 50%;
}

.loader::after{
    opacity: 0;
    animation-delay: 1s;
}
