@import url('https://fonts.googleapis.com/css2?family=Jolly+Lodger&family=Jost:wght@500&display=swap');


.editContainer{
    width:416px ;
    height: 370px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    background-color: rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(2px);
    border-radius: 20px ;
    position: absolute;
    left: -200px;

    .closeEdit{
        position: absolute;
        right: 40px;
        top: 10px;
        font-family: 'Jost', Cursive;
        color: gray;
        font-size: 25px;
        cursor: pointer;
    }

    h1{
        font-family: 'Jolly Lodger', cursive;
        font-size: 62px;
        height: 30px;
        margin: 0;
        color: rgba(221, 122, 31, 1);
        margin-bottom: 10px;
    }

    .inputGroup{
    position: relative;
    --primary: rgba(221, 122, 31, 1);

    .text{
        all: unset;
        border: none;
        width: 350px;
        height: 64px;
        border-radius: 15px;
        background-color: rgba(255, 255, 255, 0.7);
        font-family: 'Jost', Cursive;
        text-align: center;
        font-size: 20px;
        box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
        transition: 150ms cubic-bezier(0.4, 0, 0.2, 1);
    }
    .label{
        position: absolute;
        top: 20px;
        left: 30px;
        pointer-events: none;
        transition: 150ms cubic-bezier(0.4, 0, 0.2, 1);
        font-family: 'Jost', Cursive;
        color: gray;
    }
   
    .text:is(:focus, :valid) ~ label{
        transform: 
            translateY(-170%)
            scale(0.9);
            padding-inline: 0.3rem;
            color: white;
           
    }
}}


.editSave{
    border: none;
    width: 130px;
    height: 60px;
    background-color: rgba(221, 122, 31, 1);
    font-family: 'Jolly Lodger', cursive;
    font-size: 50px ;
    color: white;
    border-radius: 15px;
    cursor: pointer;
}